import React from "react"
import { GatsbyImage } from "gatsby-plugin-image"
import { graphql, useStaticQuery } from "gatsby"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import { faChevronDown } from "@fortawesome/free-solid-svg-icons"
import { faChevronLeft } from "@fortawesome/free-solid-svg-icons"

import "@fortawesome/fontawesome-svg-core/styles.css"
import { config } from "@fortawesome/fontawesome-svg-core"

import { CategorySelector } from "./category"
import { useCategory } from "../category/categoryState"

config.autoAddCss = false

export default function Header() {
  const data = useStaticQuery(graphql`
    {
      allFile(filter: { name: { eq: "2" } }) {
        edges {
          node {
            childImageSharp {
              gatsbyImageData(
                layout: CONSTRAINED
                placeholder: TRACED_SVG
                height: 600
                width: 600
              )
            }
          }
        }
      }
    }
  `)

  return (
    <header className="header">
      <Introduction data={data} />
      <Category />
    </header>
  )
}

const Introduction = React.memo(({ data }) => {
  return (
    <section className="me">
      <figure className="icon">
        <GatsbyImage
          image={data.allFile.edges[0].node.childImageSharp.gatsbyImageData}
          style={{ border: "3px solid grey", borderRadius: "50%" }}
          alt=""
        />
      </figure>
      <h2></h2>
      <p>
        C#React
      </p>
    </section>
  )
})

function Category() {
  const [isDisplayed, setIsDisplayed] = React.useState(true)
  const [category, setCategory] = useCategory()

  return (
    <section>
      <h3 style={{ textAlign: "center" }}>
        <button
          type="button"
          onClick={() => setIsDisplayed(!isDisplayed)}
          style={{
            backgroundColor: "white",
            border: "none",
            fontSize: "inherit",
            fontWeight: "inherit",
            cursor: "pointer",
          }}
        >
          
          <FontAwesomeIcon
            icon={isDisplayed ? faChevronDown : faChevronLeft}
            style={{ margin: "0 0.5rem", opacity: "0.5" }}
          />
        </button>
      </h3>

      <div style={{ display: isDisplayed ? "block" : "none" }}>
        <button
          className="reset"
          style={{
            cursor: "pointer",
          }}
          onClick={() => category.forEach((_, key) => setCategory(key, false))}
        >
          
        </button>
        <CategorySelector />
      </div>
    </section>
  )
}
